import React from 'react'

const Item = ({ item }) => {
  const onCodeClicked = () => {
    window.open(item.codeURL)
  }
  
  const onViewClicked = () => {
    window.open(item.viewURL)
  }
  
  const thisStyle = {
    background: item.backgroundColor,
  }

  return (
    <div className='item' style={thisStyle}>
      <div className={`back-image ${item.classname}`}></div>
      <div className='title'>
        { item.name }
      </div>
      <div className='button-panel'>
        <div className='button' onClick={onCodeClicked}>
          <div className='text'>
            code
          </div>
        </div>
        <div className='button' onClick={onViewClicked}>
          <div className='text'>
            view
          </div>
        </div>
      </div>
    </div>
  )
}

export default Item